.SystemUser{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 2vw;
}

.SystemUser--addUser{
    margin-top: 2.8vw;
    width: 30%;
    height: 20vw;

}

.SystemUser--addform{
    margin-top: 1vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: .5vw;
    align-items: left;
    width: 100%;
}

.SystemUser--half{
    width: 50%;
}

.SystemUser--welcome{
    width: 48%;
    position: absolute;
}

.SystemUser--contents{
    margin-top: 2.8vw;
    width: 100%;
    height: 83vh;
    display: flex;
    flex-direction: column;
}

.SystemUser--header{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.SystemUser--header span{
    display: flex;
    align-items: center;
}

.SystemUser--body{
    padding: 0vw 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
}

.SystemUser--table{
    width: 100%;
    height: 32.5vw;
}

.Box--search{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: .4vw;
}

.SystemUser--delete{
    width: 100%;
    display: flex;
    gap: 1vw;
    height: 4vw;
    padding: 10px;
}