.AddDevice{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AddDevice--welcome{
    width: 95%;
    text-align: left;
    border-radius: 10px;
    display: flex;
    align-items: center;
}


.AddDevice--contents{
    margin-top: 2.8vw;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.AddDevice--header{
    padding: 0px 15px;
    display: flex;
    justify-content: space-between;
}

.AddDevice--header span{
    display: flex;
    align-items: center;
}

.AddDevice--body{
    padding: 2vw 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 1vw;
}

.AddDevice--form{
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2vw;
}