.ItemStocks{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ItemStocks--welcome{
    width: 98%;
    text-align: left;
    padding: 10px;
    border-radius: 10px;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ItemStocks--contents{
    margin-top: 2.8vw;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.ItemStocks--header{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ItemStocks--header span{
    display: flex;
    align-items: center;
}

.ItemStocks--body{
    padding: 2vw 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: baseline;
    gap: 1vw;
}

.ItemStock--table{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 61.1vh;
}

.ItemStock--search{
    display: flex;
    justify-content: end;
}

.search{
    padding: 10px;
}

