.AssignItems{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AssignItems--welcome{
    width: 98%;
    text-align: left;
    padding: 10px;
    border-radius: 10px;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.AssignItems--contents{
    margin-top: 2.8vw;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.AssignItems--header{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.AssignItems--header span{
    display: flex;
    align-items: center;
}

.AssignItems--body{
    padding: 2vw 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: baseline;
    gap: 1vw;
}

.AssignItems--table{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 61.1vh;
}

.AssignItems--search{
    display: flex;
    justify-content: space-between;
    height: 3vw;
    margin-bottom: .4vw;
}

.AssignItems--location{
    display: flex;
    align-items: center;
    gap: .4vw;
    padding: 10px 0;
}

.AssignItems--adds{
    display: flex;
    align-items: right;
    gap: .4vw;
}