.Sidebar{
    position: fixed;
    width: 16vw;
    top: 4vw;
    left: 1vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1vw;
    padding-top: 2vw;
}

.Sidebar--logo{
    width: 50%;
}

.Sidebar--menu{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -1vw;
}

.menu--main{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 87%;
}

.menu--main li{
    list-style: none;
    border-bottom: #000 solid 1px;
    border-left: #000 solid 1px;
    border-right: #000 solid 1px;
    width: 115%;
    margin-left: -2.1vw;
    padding: 10px 0;
}

.menu--main li:first-child{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-top: #000 1px solid;
}

.menu--main li:last-child{
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom: #000 1px solid;
}

.menu--main li:hover{
    cursor: pointer;
}

.ManageItems li{
    display: block;
    border-left: none;
    border-right: none;
    margin-left: -10%;
    background-color: aliceblue;
}
.ManageItems li:first-child{
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}
.ManageItems li:last-child{
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom: none;
}
.ManageItems{
    margin-top: 10px;
    margin-bottom: -10px;
}