.ActivityLog{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ActivityLog--welcome{
    width: 99%;
    padding: 8px;
    border-radius: 10px;
    position: absolute;
    display: flex;
    align-items: center;
}

.ActivityLog--contents{
    margin-top: 3vw;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.ActivityLog--header{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ActivityLog--header span{
    display: flex;
    align-items: center;
}

.ActivityLog--body{
    padding: 2vw 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: baseline;
    gap: 1vw;
}

.ActivityLog--table{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 59.9vh;
}

.ActivityLog--search{
    display: flex;
    justify-content: end;
}

.search{
    padding: 10px;
}

