.Dashboard{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dashboard--welcome{
    width: 98%;
    text-align: left;
    border-radius: 10px;
    position: absolute;
    display: flex;
    align-items: center;
}


.dashboard--contents{
    margin-top: 2.8vw;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.dashboard--header{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
}

.dashboard--header span{
    display: flex;
    align-items: center;
}

.dashboard--body{
    padding: 2vw 10px;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    gap: 1vw;
}

.dashboard--boxes{
    margin-top: 0vw;
    border-radius: 5px;
    width: 100%;
    height: 14vw;
    display: flex;
    flex-direction: column;
    gap:0vw;
}

.dashboard--info{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 65%;
    gap: 3vw;
}

.dashboard--icons{
    font-size: 6em;
    margin-left: 2.5vw;
}

.countdetails{
    margin-right: 2.5vw;
    display: flex;
    flex-direction: column;
    color: white;
}

.countdetails span{
    font-size: 3em;
    font-weight: 500;
}

.dashboard--links{
    height: 3vw;
    display: flex;
    justify-content: space-between;
}

.dashboard--links:hover{
    cursor: pointer;
}