.Navbar{
    display: flex;
    position: fixed;
    border-bottom: 1px #000 solid;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(58,89,152);
    color: white;
    height: 3vw;
}

.Navbar--title{
    color: white;
    margin-left: 2vw;
    display: flex;
    gap: 10px;
    align-items: center;
}

.Navbar--dropdown{
    margin-right: 2vw;
}

.Navbar--dropdown ul{
    display: flex;
    gap: 10px
}

.Navbar--dropdown li{
    list-style: none;
    padding: 10px;
    margin:0;

}

.Navbar--dropdown li:hover{
    cursor: pointer;
}