.ItemType{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.ItemType--addDevice{
    margin-top: 2vw;
    width: 30%;
    height: 20vh;

}

.ItemType--addform{
    margin-top: 1vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: .5vw;
    align-items: left;
    width: 100%;
}

.ItemType--half{
    width: 50%;
}

.ItemType--welcome{
    width: 48%;
    position: absolute;
}

.ItemType--contents{
    margin-top: 2.8vw;
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
}

.ItemType--header{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ItemType--header span{
    display: flex;
    align-items: center;
}

.ItemType--body{
    padding: 0vw 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
}

.ItemType--table{
    width: 100%;
    height: 29.7vw;
}

.Box--search{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: .4vw;
}

.ItemType--delete{
    width: 100%;
    display: flex;
    gap: 1vw;
    height: 4vw;
    padding: 10px;
}